import logo from "./logo.svg";
import "./App.css";
import Title from "./components/Title";
import Select from "./components/Select";
import Wait from "./components/Wait";
import Result from "./components/Result";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { incTotalCnt, decTotalCnt } from "./store/totalCnt";
import { nextScene, setScene, resetScene } from "./store/scene";
import { nextTurn, resetTurn } from "./store/turn";
import { incCnt, decCnt, resetCnt } from "./store/cnt";
import { toggleFlag } from "./store/resultFlag";
import { motion } from "framer-motion";
import Confetti from "react-confetti";
import Invalid from "./components/Invalid";

function App() {
  let scene = useSelector((state) => state.scene.value);
  let turn = useSelector((state) => state.turn.value);
  let resultFlag = useSelector((state) => state.resultFlag.value);
  let cnt;

  let dispatch = useDispatch();

  const isMobile = {
    Android: function() {
        return navigator.userAgent.match(/Android/i);
    },
    BlackBerry: function() {
        return navigator.userAgent.match(/BlackBerry/i);
    },
    iOS: function() {
        return navigator.userAgent.match(/iPhone|iPad|iPod/i);
    },
    Opera: function() {
        return navigator.userAgent.match(/Opera Mini/i);
    },
    Windows: function() {
        return navigator.userAgent.match(/IEMobile/i) || navigator.userAgent.match(/WPDesktop/i);
    },
    any: function() {
        return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows());
    }
  };
  
  if (!isMobile.any()) {
    return (
      <div className="App">
        <Invalid />
      </div>);
  }

  switch (scene) {
    case 0:
      // main
      return (
        <div className="App">
          <Title></Title>
        </div>
      );
      break;
    case 1:
      // wait
      return (
        <div className="App" onClick={() => dispatch(nextScene())}>
          <Wait></Wait>
        </div>
      );
      break;
    case 2:
      // ox
      return (
        <div className="App">
          <Select></Select>
        </div>
      );
      break;
    case 3:
      // result
      if (!resultFlag) {
        return (
          <div className="App">
            <Result cnt={cnt}></Result>
          </div>
        );
      } else {
        return (
          <div className="App">
            <Confetti width={1000} height={1000} colors={["#FFFFFF", "#F3EDE7", "#E6E9DA"]} />
            <Result cnt={cnt}></Result>
          </div>
        );
      }
      break;
  }
}

export default App;
