import React, { useCallback, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { incTotalCnt, decTotalCnt } from "../store/totalCnt";
import { nextScene, prevScene, resetScene } from "../store/scene";
import { motion } from "framer-motion";

function Title() {
  let curCnt = useSelector((state) => state.totalCnt.value);
  let dispatch = useDispatch();

  return (
    <motion.div
      id="container"
      initial={{ rotate: 270, scale: 0 }}
      animate={{ rotate: 360, scale: 1 }}
      transition={{
        type: "spring",
        stiffness: 260,
        damping: 20,
      }}
    >
      <h1 id="titleText">
        
        <br />
        
      </h1>
      <p id="desc"> --------- &nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp; --------- </p>
      <div id="titleCntDiv">
        <span id="titleCntButton" onClick={() => dispatch(decTotalCnt())}>
          
        </span>
        <span id="titleCnt"> {curCnt} </span>
        <span id="titleCntButton" onClick={() => dispatch(incTotalCnt())}>
          
        </span>
      </div>
      <motion.div
        id="titleStartButton"
        onClick={() => dispatch(nextScene())}
        whileHover={{ scale: 1.05 }}
        whileTap={{
          scale: 0.95,
        }}
      >
         
      </motion.div>
    </motion.div>
  );
}

export default Title;
