import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { incTotalCnt, decTotalCnt } from "../store/totalCnt";
import { nextScene, prevScene, resetScene } from "../store/scene";
import icon from "../resources/icon01.png";

function Wait() {
  let turn = useSelector((state) => state.turn.value);

  return (
    <div id="container">
      <h3 id="waitHeader"> {turn}  </h3> <br />
      <p id="desc">
        <img id="imgIcon" src={icon} width="40px" height="40px"></img>
        <strong>!</strong>
        <br />
        O, X  
        <br />
         ?
        <br />
        <br />
          !
      </p>
    </div>
  );
}

export default Wait;
