import { configureStore, createSlice } from "@reduxjs/toolkit";

const scene = createSlice({
  name: "scene",
  initialState: { value: 0 },
  reducers: {
    nextScene(state) {
      state.value += 1;
    },
    prevScene(state) {
      state.value -= 1;
    },
    resetScene(state) {
      state.value = 0;
    },
  },
});

export const { nextScene, prevScene, resetScene } = scene.actions;
export default scene.reducer;
