import { configureStore } from "@reduxjs/toolkit";
import totalCntReducer from "./totalCnt";
import sceneReducer from "./scene";
import turnReducer from "./turn";
import cntReducer from "./cnt";
import resultFlagReducer from "./resultFlag";

export const store = configureStore({
  reducer: {
    totalCnt: totalCntReducer,
    scene: sceneReducer,
    turn: turnReducer,
    cnt: cntReducer,
    resultFlag: resultFlagReducer,
  },
});
