import { configureStore, createSlice } from "@reduxjs/toolkit";

const totalCnt = createSlice({
  name: "totalCnt",
  initialState: { value: 3 },
  reducers: {
    incTotalCnt(state) {
      if (state.value < 30) state.value += 1;
    },
    decTotalCnt(state) {
      if (state.value > 1) state.value -= 1;
    },
  },
});

export const { incTotalCnt, decTotalCnt } = totalCnt.actions;
export default totalCnt.reducer;
