import React from "react";
import SelectO from "./SelectO";
import SelectX from "./SelectX";
import { useDispatch, useSelector } from "react-redux";
import { incTotalCnt, decTotalCnt } from "../store/totalCnt";
import { nextScene, prevScene, resetScene } from "../store/scene";
import icon from "../resources/icon02.png";

function Select() {
  let turn = useSelector((state) => state.turn.value);

  return (
    <div id="container">
      <h3 id="waitHeader"> {turn}  </h3> <br />
      <p id="desc">
        <img id="imgIcon" src={icon} width="40px" height="40px"></img>
         ?
      </p>
      <br />
      <SelectO></SelectO>
      <SelectX></SelectX>
    </div>
  );
}

export default Select;
