import React, { useCallback, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { motion } from "framer-motion";
import icon from "../resources/icon01.png";


function Invalid() {
  let curCnt = useSelector((state) => state.totalCnt.value);
  let dispatch = useDispatch();

  return (
    <motion.div
      id="container"
      initial={{ rotate: 270, scale: 0 }}
      animate={{ rotate: 360, scale: 1 }}
      transition={{
        type: "spring",
        stiffness: 260,
        damping: 20,
      }}
    >
            <p id="desc">
        <img id="imgIcon" src={icon} width="40px" height="40px"></img>
        <strong> !</strong>
        <br />
        PC
        <br />
          .
      </p>
      <motion.div
        id="titleStartButton"
              onClick={() => { window.location.reload(); }}
        whileHover={{ scale: 1.05 }}
        whileTap={{
          scale: 0.95,
        }}
      >
      </motion.div>
    </motion.div>
  );
}

export default Invalid;
