import { configureStore, createSlice } from "@reduxjs/toolkit";

const turn = createSlice({
  name: "turn",
  initialState: { value: 1 },
  reducers: {
    nextTurn(state) {
      state.value += 1;
    },
    resetTurn(state) {
      state.value = 1;
    },
  },
});

export const { nextTurn, resetTurn } = turn.actions;
export default turn.reducer;
