import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { useState } from "react";
import { incTotalCnt, decTotalCnt } from "../store/totalCnt";
import { nextScene, prevScene, resetScene } from "../store/scene";
import { incCnt, decCnt, resetCnt } from "../store/cnt";
import { nextTurn, resetTurn } from "../store/turn";
import lockIcon from "../resources/icon03.png";
import paperIcon from "../resources/icon04.png";
import { motion } from "framer-motion";
import { toggleFlag } from "../store/resultFlag";

function Result() {
  let dispatch = useDispatch();
  let cnt = useSelector((state) => state.cnt.value);
  let resultFlag = useSelector((state) => state.resultFlag.value);

  if (!resultFlag)
    return (
      <motion.div
        id="container"
        initial={{ rotate: 270, scale: 0 }}
        animate={{ rotate: 360, scale: 1 }}
        transition={{
          type: "spring",
          stiffness: 260,
          damping: 20,
        }}
      >
        <div>
          <p id="desc-small">   .</p>
          <img id="resultImgIcon" src={lockIcon} width="70px" height="70px"></img>
          <motion.div
            id="titleStartButton"
            onClick={() => dispatch(toggleFlag())}
            whileHover={{ scale: 1.05 }}
            whileTap={{
              scale: 0.95,
            }}
          >
             
          </motion.div>
        </div>
      </motion.div>
    );
  else
    return (
      <div id="container">
        <div>
          <motion.img
            id="resultImgIcon"
            src={paperIcon}
            width="70px"
            height="70px"
            whileHover={{ scale: 1.2 }}
            whileTap={{ scale: 0.9 }}
            transition={{ type: "spring", stiffness: 400, damping: 17 }}
          ></motion.img>
          <h1>{cnt}</h1>
          <motion.div
            id="titleStartButton"
            onClick={() => {
              dispatch(resetTurn());
              dispatch(resetCnt());
              dispatch(toggleFlag());
              dispatch(resetScene());
            }}
            whileHover={{ scale: 1.05 }}
            whileTap={{
              scale: 0.95,
            }}
          >
             
          </motion.div>
        </div>
      </div>
    );
}

export default Result;
