import { configureStore, createSlice } from "@reduxjs/toolkit";

const cnt = createSlice({
  name: "cnt",
  initialState: { value: 0 },
  reducers: {
    incCnt(state) {
      state.value += 1;
    },
    decCnt(state) {
      state.value -= 1;
    },
    resetCnt(state) {
      state.value = 0;
    },
  },
});

export const { incCnt, decCnt, resetCnt } = cnt.actions;
export default cnt.reducer;
