import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { incTotalCnt, decTotalCnt } from "../store/totalCnt";
import { nextScene, prevScene, resetScene } from "../store/scene";
import { incCnt, decCnt, resetCnt } from "../store/cnt";
import { nextTurn, resetTurn } from "../store/turn";
import { motion } from "framer-motion";

function SelectO() {
  let dispatch = useDispatch();
  let turn = useSelector((state) => state.turn.value);
  let totalCnt = useSelector((state) => state.totalCnt.value);

  return (
    <motion.div
      id="OXButton"
      onClick={() => {
        dispatch(incCnt());
        dispatch(nextTurn());
        if (turn < totalCnt) dispatch(prevScene());
        else dispatch(nextScene());
      }}
      whileHover={{ scale: 1.05 }}
      whileTap={{
        scale: 0.95,
      }}
    >
      O
    </motion.div>
  );
}

export default SelectO;
